import store from "@/store";
import gradeModel from "@/model/grade";

export function searchHero(id) {
    return store.state.HeroSource[id];
}

export function searchHeroText(text) {
    // 正则表达式匹配：[{id}]{content}
    const id = text.match(/\[(\d+)\]/)[1];
    return store.state.HeroSource[id];
}

export function searchSkill(id) {
    return store.state.SkillSource[id];
}

export function searchSkillText(text) {
    // 正则表达式匹配：[{id}]{content}
    const id = text.match(/\[(\d+)\]/)[1];
    return store.state.SkillSource[id];
}

export function searchItem(id) {
    return store.state.ItemSource[id];
}

export function searchItemText(text) {
    // 正则表达式匹配：[{id}]{content}
    const id = text.match(/\[(\d+)\]/)[1];
    return store.state.ItemSource[id];
}

export function searchStage(id) {
    return store.state.StageSource[id];
}

export function searchSuit(wp, ar, pt) {
    const key = `${wp}-${ar}-${pt}`;
    return store.state.Indexes.SuitIndex[key];
}

export function searchTitle(id) {
    return store.state.Indexes.TitleIndex[id];
}

export function searchSkillTitleIndex(id, type) {
    if (type == 10) {
        return store.state.Indexes.TitleIndex[id];
    } else {
        return store.state.Indexes.SkillIndex[id];
    }
}

export function searchMatchByTarget(id) {
    return store.state.Indexes.MatchIndex[id] || [];
}

export function searchSkillConfigs(id) {
    return store.state.Indexes.SkillConfigIndex[id]
}

export function searchSkillExtConfig(id) {
    return store.state.SkillExtConfigSource[id]
}

export function searchEventName(id) {
    return store.state.EventInfoSource[id].name
}

export function bindSkillAndExt() {
    Object.entries(store.state.SkillExtConfigSource).forEach(([id, ext]) => {
        const skill = searchSkill(id);
        const configs = searchSkillConfigs(id) || [];
        if (ext['range']) {
            skill['ext_range'] = ext['range']
        }
        if (ext['grade']) {
            skill['ext_grade'] = ext['grade'];
        }
        if (ext['attrs']) {
            skill['ext_attr'] = ext['attrs'];
            ext['attrs'].forEach(attr => {
                const config = configs.find(config => config['skillType'] === attr['type'])
                if (config) {
                    config['ext'] = attr
                } else {
                    const skill = searchSkill(id)
                    const newConfig = {
                        name: skill['name'],
                        skillId: id,
                        skillType: attr['type'],
                        ext: attr
                    }
                    store.state.SkillConfigSource.push(newConfig)
                    if (store.state.Indexes.SkillConfigIndex[id]) {
                        store.state.Indexes.SkillConfigIndex[id].push(newConfig)
                    } else {
                        store.state.Indexes.SkillConfigIndex[id] = [newConfig]
                    }
                }
            })
        }
    });
}

export function calcSkillLevel() {
    Object.entries(store.state.ItemSource).forEach(([, item]) => {
        if (item.targetSkill > 0) {
            const skill = searchSkill(item.targetSkill)
            if (skill) {
                switch(skill.type) {
                    case 0: skill['level'] = item.attr_ts; break;
                    case 1: skill['level'] = item.attr_fs; break;
                    case 2: skill['level'] = item.attr_ss; break;
                    case 3: skill['level'] = item.attr_ws; break;
                    case 4: skill['level'] = item.attr_rs; break;
                }
            }
        }
    });
}

export function initTitleSkill() {
    Object.values(store.state.TitleConfigSource).forEach((title) => {
        title['id'] = title['skillId']
        title['type'] = 10
    });
}

export function calcSkillGrade() {
    Object.values(store.state.SkillSource).forEach(skill => {
        // 防功标记
        const defFlag = skill.type == 5 && skill.levelType == 7
        const grade = gradeModel.calcSkillGrade(skill.id, {defFlag: defFlag})
        skill['grade'] = grade.totalGrade.toFixed(2)
        skill['grade_ext'] = () => `${skill['name']}分值计算\n` + grade.detail()
        skill['gen_desc'] = grade['desc']
        // 护体分值
        if (skill.type == 5 && (skill.levelType == 4 || skill.levelType == 5)) {
            const gradeUp = gradeModel.calcSkillGrade(skill.id, {upFlag: true})
            skill['grade_up'] = gradeUp.totalGrade.toFixed(2)
            skill['grade_up_ext'] = () => `${skill['name']}护体分值计算\n` + gradeUp.detail()
        }
        // 防功使用效果计算
        if (defFlag) {
            const gradeUse = gradeModel.calcSkillGrade(skill.id, {useFlag: true, defFlag: true})
            skill['grade_up'] = gradeUse.totalGrade.toFixed(2)
            skill['grade_up_ext'] = () => `${skill['name']}防功使用分值计算\n` + gradeUse.detail()
        }
    });
}

export function calcHeroGrade() {
    Object.values(store.state.HeroSource).forEach(hero => {
        // 首先设置套装字段
        const suit = searchSuit(hero['equip_wp'], hero['equip_ar'], hero['equip_pt'])
        if (suit) {
            hero['suit'] = suit.name
            hero['suit_bs'] = suit.bsId
            hero['suit_fs'] = suit.fsId
            hero['suit_talent'] = suit.talentId
        }

        const getSkillGrade = (skillId) => {
            const skill = searchSkill(skillId)
            if (skill) {
                return Number(skill.grade) || 0
            } else {
                return 0
            }
        }
        let totalGrade = 0
        let keGrade = 0
        // 天赋分值
        hero['grade_talent'] = getSkillGrade(hero['talent']) + getSkillGrade(hero['suit_talent'])
        totalGrade += hero['grade_talent']
        keGrade += getSkillGrade(hero['talent'])
        // 内功分值
        hero['grade_bs'] = getSkillGrade(hero['equip_bs']) + getSkillGrade(hero['suit_bs'])
        totalGrade += hero['grade_bs']
        // 轻功分值
        hero['grade_fs'] = getSkillGrade(hero['equip_fs']) + getSkillGrade(hero['suit_fs'])
        totalGrade += hero['grade_fs']
        // 特技分值（武功出招特技分值0.4，武功栏特技按分值顺序排列，系数分别为0.8、0.4、0.2）
        hero['grade_ss'] = 0
        const ss = []
        for (let i = 0; i < 30; i++ ) {
            const s = hero['skills'][i]
            if (s['skill']) {
                const skill = searchSkill(s['skill'])
                if (skill && skill['type'] == 8) {
                    ss.push(Number(skill.grade) || 0)
                } else if (skill['type'] == 5 && skill['levelType'] == 7) {
                    ss.push(Number(skill.grade_up) || 0)
                }
            }
        }
        // 对特技分值排序
        ss.sort((a, b) => b - a)
        for (let i = 0; i < ss.length; i++) {
            if (i == 0) {
                hero['grade_ss'] += ss[i] * 0.8
                keGrade += ss[i] > 10 ? ss[i] * 2/3 : ss[i]
            } else if (i == 1) {
                hero['grade_ss'] += ss[i] * 0.4
            } else if (i == 2) {
                hero['grade_ss'] += ss[i] * 0.2
            }
        }
        // 专精武功出招特技
        if (hero['mastery']) {
            const mastery = searchSkill(hero['mastery'])
            if (mastery && mastery['trigger']) {
                mastery['trigger'].forEach(id => {
                    const triggerSkill = searchSkill(id)
                    // 特技或防功
                    if (triggerSkill['type'] == 8) {
                        hero['grade_ss'] += Number(triggerSkill['grade'] || 0) * 0.4
                        keGrade += Number(triggerSkill['grade'] || 0) * 0.5
                    } else if (triggerSkill['type'] == 5 && triggerSkill['levelType'] == 7) {
                        hero['grade_ss'] += Number(triggerSkill['grade_up'] || 0) * 0.4
                        keGrade += Number(triggerSkill['grade_up'] || 0) * 0.5
                    }
                })
            }
        }
        totalGrade += hero['grade_ss']

        hero['grade_ke'] = keGrade
        hero['grade_total'] = totalGrade
    });
}

export function createSkillIndex() {
    store.state.Indexes.SkillIndex = {};
    Object.values(store.state.SkillSource).forEach(skill => {
        store.state.Indexes.SkillIndex[skill.id] = {};
    });
    // 建立拥有者映射
    Object.values(store.state.HeroSource).forEach(hero => {
        resolveSkillOwner(hero)
    });
    // 洗武
    store.state.EventSource['xw'].forEach(event => {
        resolveXwEvent(event)
    });

    // 建立威力加成映射
    store.state.ListSource['match1'].forEach(match => {
        const skillIndex = store.state.Indexes.SkillIndex[match['matchSkillId']];
        skillIndex['match'] = skillIndex['match'] || [];
        skillIndex['match'].push(`[${match['itemId']}]${searchItem(match['itemId']).name}+${match['power']}`);
        const matchIndex = store.state.Indexes.MatchIndex[match['matchSkillId']] || [];
        matchIndex.push({type: 'item', ...match});
        store.state.Indexes.MatchIndex[match['matchSkillId']] = matchIndex;
    });
    store.state.ListSource['match2'].forEach(match => {
        const skillIndex = store.state.Indexes.SkillIndex[match['matchSkillId']];
        skillIndex['match'] = skillIndex['match'] || [];
        skillIndex['match'].push(`[${match['skillId']}]${searchSkill(match['skillId']).name}+${match['power']}`);
        const matchIndex = store.state.Indexes.MatchIndex[match['matchSkillId']] || [];
        matchIndex.push({type: 'skill', ...match});
        store.state.Indexes.MatchIndex[match['matchSkillId']] = matchIndex;
    });

    // 建立外功出招映射
    Object.values(store.state.SkillSource).forEach(skill => {
        if (skill['trigBy'] && skill['trigBy'] > 0) {
            const triggedSkillIndex = store.state.Indexes.SkillIndex[skill['trigBy']]
            triggedSkillIndex['trigger'] = triggedSkillIndex['trigger'] || [];
            const rate = skill['attr_trig'] ? skill['attr_trig'] : 0;
            let text = `${rate}% [${skill.id}]${skill.name}`
            if (skill['attrl_power'] > 0) {
                text += ` (${skill['attrl_power']})`
            }
            if (skill['type'] == 8) {
                text += `(特技)`
            } else if (skill['type'] == 5) {
                text += `(防功)`
            }
            triggedSkillIndex['trigger'].push(text);
            // 填充原始数据
            const triggerSkill = searchSkill(skill['trigBy'])
            triggerSkill['trigger'] = triggerSkill['trigger'] || [];
            triggerSkill['trigger'].push(skill['id']);
        }
    });
}

function addSkillIndex(hero, skillId, option) {
    option = option || {};
    if (!skillId || skillId <= 0) {
        return
    }
    const skillIndex = option.title ? store.state.Indexes.TitleIndex[skillId] : store.state.Indexes.SkillIndex[skillId];
    if(!skillIndex) {
        return
    }
    const text = option.suffix ? `[${hero.id}]${hero.name}(${option.suffix})` : `[${hero.id}]${hero.name}`
    if (hero['friend']) {
        skillIndex['owner_friend'] = skillIndex['owner_friend'] || [];
        if (!skillIndex['owner_friend'].includes(text)) {
            skillIndex['owner_friend'].push(text);
        }
    } else {
        skillIndex['owner_npc'] = skillIndex['owner_npc'] || [];
        if (!skillIndex['owner_npc'].includes(text)) {
            skillIndex['owner_npc'].push(text);
        }
    }
}

function resolveSkillOwner(hero) {
    const addFunc = (skillId, option) => {
        addSkillIndex(hero, skillId, option)
    }
    // 内轻天赋称号
    addFunc(hero['equip_bs'])
    addFunc(hero['equip_fs'])
    addFunc(hero['talent'])
    addFunc(hero['title'], {title: true})

    // 套装
    const suit = searchSuit(hero['equip_wp'], hero['equip_ar'], hero['equip_pt'])
    if (suit) {
        addFunc(suit.bsId, {suffix: '套装'})
        addFunc(suit.fsId, {suffix: '套装'})
        addFunc(suit.talentId, {suffix: '套装'})
    }

    // 武器武功
    if (hero['equip_wp'] > 0) {
        const item = searchItem(hero['equip_wp'])
        if (item['useSkill'] > 0) {
            addFunc(item['useSkill'], {suffix: '武器'})
        }
    }

    // 武功栏位
    hero['skills'].forEach((skill) => {
        const skillId = skill['skill']
        if (skillId <= 0) {
            return
        }
        // 内轻防功特殊处理
        const skillData = searchSkill(skillId)
        if (skillData.type == 5 || skillData.type == 6) {
            if (skillData.levelType == 7) {
                addFunc(skillId, {suffix: `防功`})
            }
        } else {
            // 专精后缀
            if (hero['mastery'] == skillId) {
                addFunc(skillId, {suffix: `专精`})
            } else {
                addFunc(skillId)
            }
        }
    })
}

function resolveXwEvent(event) {
    const heroId = event['index']
    const skillId = event['index2']
    const slot = event['value']
    const hero = searchHero(heroId)
    const op = event['op']
    if (op == '洗武功') {
        addSkillIndex(hero, skillId, {suffix: `洗${slot+1}格`})
    } else if (op == '洗天赋') {
        addSkillIndex(hero, skillId, {suffix: `洗天赋`})
    } else if (op == '洗专精') {
        addSkillIndex(hero, skillId, {suffix: `洗专精`})
    }
}

export function searchFriendConf(id) {
    return store.state.Const.friend[id];
}

export function initItemIndex() {
    // 建立拥有者映射
    Object.values(store.state.HeroSource).forEach(hero => {
        for (let i = 0; i < 4; i++ ) {
            const it = hero['items'][i]
            if (it['item'] && it['item'] > 0) {
                const item = searchItem(it['item'])
                if (hero['friend']) {
                    item['owner_friend'] = item['owner_friend'] || [];
                    item['owner_friend'].push(`[${hero.id}]${hero.name}`);
                } else {
                    item['owner_npc'] = item['owner_npc'] || [];
                    item['owner_npc'].push(`[${hero.id}]${hero.name}`);
                }
            }
        }
    });
}

export function initSuitIndex() {
    // 建立拥有者映射
    Object.values(store.state.HeroSource).forEach(hero => {
        resolveSuitOwner(hero)
    });
}

function resolveSuitOwner(hero) {
    const suit = searchSuit(hero['equip_wp'], hero['equip_ar'], hero['equip_pt'])
    if (suit) {
        if (hero['friend']) {
            suit['owner_friend'] = suit['owner_friend'] || [];
            suit['owner_friend'].push(`[${hero.id}]${hero.name}`);
        } else {
            suit['owner_npc'] = suit['owner_npc'] || [];
            suit['owner_npc'].push(`[${hero.id}]${hero.name}`);
        }
    }
}